import React from "react";
import { Link } from "react-router-dom";

const AlertSystem = ({ message, showLink }) => {
  return message ? (
    <div
      className="ui alert alert-warning alert-dismissible fade show"
      role="alert"
    >
      {message}
      {showLink ? (
        <>
          <Link to="/new-portal">
            <strong>Click Here</strong>
          </Link>
          to create
        </>
      ) : null}
      <button type="button" className="btn-close" aria-label="Close"></button>
    </div>
  ) : null;
};

export default AlertSystem;
