import React, { useState, useLayoutEffect } from "react";
import { useNavigate } from "react-router-dom";
import axios from "axios";
import FileDownload from "js-file-download";

const DownloadCenter = ({ params }) => {
  const nav = useNavigate();

  const [downloadables, setDownloadables] = useState("");
  const [message, setMessage] = useState("");

  useLayoutEffect(() => {
    const getDownloadFileName = async () => {
      await axios
        .post("http://localhost:5985/download", { appName: params })
        .then((res) => {
          setDownloadables(res.data);
        });
    };

    getDownloadFileName();
  }, [params]);

  const downloadFile = async (download) => {
    setMessage("File Downloading Now...");

    setTimeout(() => setMessage(""), 1000);

    const getDownloadFile = {
      appName: params,
      fileName: download,
    };

    await axios
      .post("http://localhost:5985/download-file", getDownloadFile, {
        responseType: "blob",
      })
      .then((res) => {
        FileDownload(res.data, download);

        setMessage("File Downloaded Successfully... Happy Viewing 😍");

        setTimeout(() => setMessage(""), 5000);
      })
      .catch((err) => {
        if (err.response.status === 500) {
          console.log("Sad");

          setMessage("Internal Server Error");

          setTimeout(() => setMessage(""), 5000);
        } else {
          // console.log(err.response.data.msg);

          setMessage("Bad Request. File couldn't be found.. 🤦‍♂️");

          setTimeout(() => setMessage(""), 5000);
        }
        console.error("Error:", err);
      });
  };

  return (
    <section className="download-center">
      <h4 className="center">Download Center</h4>

      {message ? (
        <div
          className="ui alert alert-warning alert-dismissible fade show"
          role="alert"
        >
          {message}

          <button
            type="button"
            className="btn-close"
            aria-label="Close"
          ></button>
        </div>
      ) : null}

      {downloadables ? (
        <div className="">
          <p className="center">Click the Download Button to access files</p>
          {downloadables.map((download, index) => (
            <div key={index} className="download-area">
              {download}
              <button
                onClick={() => downloadFile(download)}
                className="btn btn-dark"
              >
                Download
              </button>
            </div>
          ))}
        </div>
      ) : (
        <section>
          <p className="center">
            No file uploaded for this portal. Click the Button to add a New File
          </p>
          <div className="center">
            <button
              onClick={() => nav("/upload-pending")}
              className="btn btn-dark"
            >
              upload new file
            </button>
          </div>
        </section>
      )}
    </section>
  );
};

export default DownloadCenter;
