import React, { useState } from "react";
import axios from "axios";
import AlertSystem from "../Components/AlertSystem";
import SideBar from "../Components/SideBar";
import Navbar from "../Components/Navbar";
// import ServerDown from "../Components/ServerDown";

const UploadPending = () => {
  const [portalSearch, setPortalSearchResults] = useState("");
  const [message, setMessage] = useState("");
  const [showLink, setShowLink] = useState(true);
  const [foundPortal, setFoundPortal] = useState(false);
  const [selectedPortal, setSelectedPortal] = useState("");

  const [searchQuery, setSearchQuery] = useState("");

  const extraData = [
    {
      BRD_Uploaded: { upload: true, fileName: "sample.doc" },
      BRS_Uploaded: { upload: false, fileName: "NULL" },
      ProductPaper: { upload: true, fileName: "sample2.pdf" },
      QA: { upload: false, fileName: "NULL" },
      UAT: { upload: false, fileName: "NULL" },
    },
  ];

  const mapFoundPortal = (portal) => {
    setPortalSearchResults("");
    setSelectedPortal(portal);
  };

  const [file, setFile] = useState();
  const [uploadPercentage, setUploadPercentage] = useState(0);

  const newFileUpload = new FormData();
  newFileUpload.append("files", file);

  const xx = async () => {
    const res = await axios
      .post("http://localhost:5985/upload", newFileUpload, portalSearch, {
        headers: { "Content-Type": "multipart/form-data" },
        onUploadProgress: (progressEvent) => {
          setUploadPercentage(
            parseInt(
              Math.round((progressEvent.loaded * 100) / progressEvent.total)
            )
          );

          setTimeout(() => setUploadPercentage(0), 10000);

          setTimeout(() => setMessage(""), 5000);
        },
      })
      .then((response) => response.json())
      .then((result) => {
        console.log("Success:", result);

        // const { fileName, filePath } = res.data;
        // setUploadedFile({ fileName, filePath });

        setMessage("File Uploaded Successfully");

        setTimeout(() => setMessage(""), 5000);
      })
      .catch((err) => {
        if (err.response.status === 500) {
          console.log("Sad");

          setMessage("Sorry! File Didnt Upload");
        } else {
          console.log(err.response.data.msg);
          setMessage(err.response.data.msg);

          setTimeout(() => setMessage(""), 5000);
        }

        console.error("Error:", err);
      });
  };

  const portalUploadCheck = async (e) => {
    setSearchQuery(e.target.value);

    if (searchQuery.length < 1) {
      await axios
        .get(`http://localhost:5985/portalUploadCheck/${searchQuery}`)
        .then((result) => {
          setPortalSearchResults(result.data);
        });
    }
    // setSearchQuery(searchQuery.toLowerCase());
    // console.log(searchQuery);
    await axios
      .get(`http://localhost:5985/portalUploadCheck/${searchQuery}`)
      .then((result) => {
        setPortalSearchResults(result.data);
        // setShowLink(false);

        // setMessage("Application(s) Found");
        // setFoundPortal(true);

        setTimeout(() => {
          setMessage("");
        }, 1500);
      })
      .catch((err) => {
        setFoundPortal(false);
        setShowLink(false);
        setMessage("Unable to retrive portals at the moment");
        console.log(err);

        setTimeout(() => {
          setMessage("");
        }, 5000);
      });
  };

  return (
    <view>
      <SideBar />
      <div className="right" style={{ padding: 0 }}>
        <Navbar />
        <AlertSystem
          message={message}
          // setMessage={setMessage}
          showLink={showLink}
        />

        <div style={{ padding: 15, marginBottom: 20 }}>
          <p style={{ fontSize: 21 }}>
            <strong>NOTE : </strong>
            <code>
              THIS PAGE ONLY SUPPORTS DOCUMENT UPLOADS FOR PREVIOUSLY UPLOADED
              PORTALS
            </code>
          </p>

          <label htmlFor="search" style={{ marginBottom: 10 }}>
            Enter a Portal Name<code>*</code>
          </label>

          <div style={{ display: "flex", flexDirection: "column" }}>
            <input
              type="text"
              list="Application"
              className="form-control mb-2"
              id="search"
              placeholder="Select a Application"
              onChange={(e) => portalUploadCheck(e)}
            />

            {portalSearch ? (
              <div className="searchResults">
                <div className="center">
                  SEARCH RESULTS ({portalSearch.length})
                </div>

                {portalSearch ? (
                  <div>
                    {portalSearch.map((portal, index) => (
                      <div
                        className="result"
                        key={index}
                        onClick={() => mapFoundPortal(portal)}
                      >
                        {portal.appName}

                        <span className="results-desc">
                          {portal.description}
                        </span>
                      </div>
                    ))}
                  </div>
                ) : (
                  <div className="center p-2">No Results</div>
                )}
              </div>
            ) : null}
          </div>

          {selectedPortal ? (
            <>
              <div className="accordion" id="accordionExample">
                <h3 className="center m-4">{selectedPortal.appName}</h3>

                {extraData.map((sp, index) => (
                  <div key={index}>
                    <details className="accordion-item accordion-body">
                      <summary className="accordion-header">
                        <strong>Document: BRD</strong>
                      </summary>
                      <hr />
                      {sp.BRD_Uploaded.upload ? (
                        <div className="accordion-body">
                          {selectedPortal.BRD_Uploaded.upload ? (
                            <img src="./pdf.svg" alt="" width="30" />
                          ) : (
                            <img src="./word.svg" alt="" width="30" />
                          )}
                          {`File Name: ${sp.BRD_Uploaded.fileName}`}
                        </div>
                      ) : (
                        <input
                          type="file"
                          name="file"
                          id="file-input"
                          className="btn btn-dark m-3"
                          required
                          onChange={(e) => setFile(e.target.files[0])}
                          // disabled={upload}
                        />
                      )}
                    </details>

                    <details className="accordion-item accordion-body">
                      <summary className="accordion-header">
                        <strong>Document Type: BRS</strong>
                      </summary>
                      <hr />
                      {selectedPortal.BRS_Uploaded.upload ? (
                        `File Name: ${sp.BRS_Uploaded.fileName}`
                      ) : (
                        <input
                          type="file"
                          name="file"
                          id="file-input"
                          className="btn btn-dark m-3"
                          required
                          onChange={(e) => setFile(e.target.files[0])}
                          // disabled={upload}
                        />
                      )}
                    </details>

                    <details className="accordion-item accordion-body">
                      <summary className="accordion-header">
                        <strong>Document Type: UAT</strong>
                      </summary>
                      <hr />
                      {selectedPortal.UAT.upload ? (
                        `File Name: ${sp.UAT.fileName}`
                      ) : (
                        <input
                          type="file"
                          name="file"
                          id="file-input"
                          className="btn btn-dark m-3"
                          required
                          onChange={(e) => setFile(e.target.files[0])}
                          // disabled={upload}
                        />
                      )}
                    </details>

                    <details className="accordion-item accordion-body">
                      <summary className="accordion-header">
                        <strong>Document Type: QA</strong>
                      </summary>
                      <hr />
                      {selectedPortal.QA.upload ? (
                        `File Name: ${sp.QA.fileName}`
                      ) : (
                        <div>
                          <input
                            type="file"
                            name="file"
                            id="file-input"
                            className="btn btn-dark m-3"
                            required
                            onChange={(e) => setFile(e.target.files[0])}
                            // disabled={upload}
                          />

                          <div className="progress">
                            <div
                              className="progress-bar progress-bar-stripped bg-success"
                              id="file"
                              style={{
                                width: `${uploadPercentage}`,
                                color: "#374151",
                              }}
                            >
                              {uploadPercentage}%
                            </div>
                          </div>
                        </div>
                      )}
                    </details>

                    <details className="accordion-item accordion-body">
                      <summary className="accordion-header">
                        <strong>Document Type: Product Paper</strong>
                      </summary>
                      <hr />
                      {selectedPortal.ProductPaper.upload ? (
                        `File Name: ${sp.ProductPaper.fileName}`
                      ) : (
                        <div>
                          <div className="progress">
                            <div
                              className="progress-bar progress-bar-stripped bg-success"
                              id="file"
                              style={{
                                width: `${uploadPercentage}`,
                                color: "#374151",
                              }}
                            >
                              {uploadPercentage}%
                            </div>
                          </div>

                          <input
                            type="file"
                            name="file"
                            id="file-input"
                            className="btn btn-dark mb-3"
                            required
                            onChange={(e) => setFile(e.target.files[0])}
                            // disabled={upload}
                          />
                        </div>
                      )}
                    </details>

                    <button
                      className="btn btn-success mt-4"
                      style={{ float: "right" }}
                    >
                      Save Changes
                    </button>
                  </div>
                ))}
              </div>
            </>
          ) : (
            <div className="center m-5">
              <h2>Start by typing into the search field above.</h2>
            </div>
          )}

          {foundPortal ? (
            <>
              <div className="center">
                <button className="btn btn-success">Proceed</button>
              </div>

              <div className="mt-4 download-center">
                <div className="progress">
                  <div
                    className="progress-bar progress-bar-stripped bg-success"
                    id="file"
                    style={{ width: `${uploadPercentage}`, color: "#374151" }}
                  >
                    {uploadPercentage}%
                  </div>
                </div>
                <br />
              </div>
            </>
          ) : null}
        </div>
      </div>
    </view>
  );
};

export default UploadPending;
