import axios from "axios";
import React, { useState, useEffect, Suspense } from "react";
import { useParams, useNavigate, useLocation } from "react-router-dom";
import AuthModal from "../Components/AuthModal";
import Navbar from "../Components/Navbar";
import SideBar from "../Components/SideBar";
import DownloadCenter from "./DownloadCenter";
const TechnicalDetails = React.lazy(() => import("./TechnicalDetails"));
const BasicInformation = React.lazy(() => import("./BasicInfo"));

const PortalPage = () => {
  const params = useParams();
  const nav = useNavigate();
  const location = useLocation();

  const [portal, setPortal] = useState();

  const auth = localStorage.getItem("userToken");

  useEffect(() => {
    // if (auth) {
    const getPortalInfoFromServer = async () => {
      await axios
        .get(`http://localhost:5985/getPortalsByAppName/${params.appName}`)
        .then((res) => {
          setPortal(res.data[0]);
          // console.log(portal, res.data[0]);
        });
    };
    getPortalInfoFromServer();
    // } else {
    //   setTimeout(() => {
    //     nav("/");
    //   }, 5000);
    // }
  }, []);

  return (
    <>
      <view>
        <SideBar />

        {/* {auth ? null : <AuthModal />} */}

        <div className="right" style={{ padding: 0 }}>
          <Navbar />
          <div className="container mt-2">
            {/* {message ? (
              <div
                className="ui alert alert-warning alert-dismissible fade show"
                role="alert"
              >
                {message}

                <button
                  type="button"
                  className="btn-close"
                  aria-label="Close"
                ></button>
              </div>
            ) : null} */}

            <Suspense fallback={<h2>fgs</h2>}>
              <div className="ui alert alert-info">
                Showing Portals managed by {params.department}
              </div>
              <section>
                <h2 className="center">{params.appName}</h2>
                <ul className="nav tabs mb-3">
                  <li
                    className={`nav-item col-2 tab ${
                      location.pathname.includes("basic-info") ? "active" : ""
                    }`}
                    aria-current="page"
                    href="#"
                    onClick={() => nav("basic-info")}
                  >
                    Basic Information
                  </li>
                  <li
                    className={`nav-item col-2 tab ${
                      location.pathname.includes("technical-details")
                        ? "active"
                        : ""
                    }`}
                    onClick={() => nav("technical-details")}
                  >
                    Technical Details
                  </li>
                  <li
                    className={`nav-item col-2 tab ${
                      location.pathname.includes("download-center")
                        ? "active"
                        : ""
                    }`}
                    onClick={() => nav("download-center")}
                  >
                    Download Center
                  </li>
                </ul>

                <section className="container" style={{ textAlign: "center" }}>
                  <div
                    className={
                      location.pathname.includes("basic-info") ? "" : "no-show"
                    }
                  >
                    <BasicInformation portal={portal} />
                  </div>
                  <div
                    className={
                      location.pathname.includes("technical-details")
                        ? ""
                        : "no-show"
                    }
                  >
                    <TechnicalDetails portal={portal} />
                  </div>
                  <div
                    className={
                      location.pathname.includes("download-center")
                        ? ""
                        : "no-show"
                    }
                  >
                    <DownloadCenter params={params.appName} />
                  </div>
                </section>
              </section>
            </Suspense>
          </div>
        </div>
      </view>
    </>
  );
};

export default PortalPage;
