import React from 'react';
import { useState, Suspense } from "react";
import Footer from "../Components/Footer";
import { useNavigate } from "react-router-dom";
import axios from "axios";

const Login = () => {
  const [emailAddress, setEmailAddress] = useState("");
  const [userPassword, setUserPassword] = useState("");
  const [showFeedback, setShowFeedback] = useState("gdm");

  const nav = useNavigate();

  console.log(window.navigator.userAgent);

  // Function to validate login
  const LoginTO = async (e) => {
    e.preventDefault();

    const loginDetails = {
      emailAddress,
      userPassword,
    };

    try {
      await axios
        .post("http://localhost:5985/login", loginDetails)
        .then((res) => {
          localStorage.setItem("user", res.data);
          // console.log(login);
        });
    } catch (err) {
      console.log(err);
      setShowFeedback(err.response.statusText);
      setTimeout(() => {
        setShowFeedback(false);
      }, 3000);

      if (e) throw e;
    } finally {
      console.log("...");

      //inITAL DEPT SELECTION PAGE

      // await fetch("http://localhost:9985/login", {
      //   headers: {
      //     "content-type": "application/json",
      //     Method: "POST",
      //   },
      //   body: loginDetails,
      // });
    }
  };

  return (
    <div
      className="center"
      style={{
        height: "100vh",
        backgroundImage: `url('./archives.jpg')`,
        backgroundRepeat: "no-repeat",
        backgroundSize: "cover",
      }}
    >
      <div
        className="center"
        style={{
          height: "60vh",
          flexDirection: "column",
        }}
      >
        {showFeedback ? (
          <div className={`alert alet-warning`}>
            <code className="center">{showFeedback}</code>
          </div>
        ) : null}

        <form>
          <div className="form">
            <Suspense fallback={<h1>Loading</h1>}>
              <label htmlFor="email" style={{ color: "#fff" }}>
                USERNAME <code>*</code>
              </label>
              <input
                inputMode="email"
                type="email"
                className="form-control"
                required
                aria-describedby="email"
                auto-complete="off"
                onChange={(e) => setEmailAddress(e.target.value)}
              />{" "}
              <br />
              <label htmlFor="password" style={{ color: "#fff" }}>
                PASSWORD <code>*</code>
              </label>
              <input
                type="password"
                required
                inputMode="password"
                className="form-control"
                aria-describedby="password"
                onChange={(e) => setUserPassword(e.target.value)}
              />
              <br />
              <div className="d-grid">
                <button
                  type="button"
                  className="btn btn-primary"
                  onClick={(e) => LoginTO(e)}
                >
                  Submit
                </button>
              </div>
            </Suspense>
          </div>
        </form>
        <Footer />
      </div>
    </div>
  );
};

export default Login;
