import React from "react";
import { Routes, Route } from "react-router-dom";
import Home from "./Pages/Home";
import Login from "./Pages/Login";
import NewUpload from "./Pages/NewUpload";
import NotaPage from "./Pages/NotaPage";
import PortalPage from "./Pages/PortalPage";
import BasicInformation from "./Pages/BasicInfo";
import TechnicalDetails from "./Pages/TechnicalDetails";
import DownloadCenter from "./Pages/BasicInfo";
import UploadPending from "./Pages/UploadPending";

const PageRouter = () => {
  return (
    <Routes>
      <>
        <Route path="/" element={<Login />} />
        <Route path="/portals" element={<Home />} />

        <Route path=":department/portals/:appName" element={<PortalPage />}>
          <Route path="basic-info" element={<BasicInformation />} />
          <Route path="technical-details" element={<TechnicalDetails />} />
          <Route path="download-center" element={<DownloadCenter />} />
        </Route>

        <Route path="new-portal" element={<NewUpload />} />
        <Route path="upload-pending" element={<UploadPending />} />
        <Route path="*" element={<NotaPage />} />
      </>
    </Routes>
  );
};

export default PageRouter;
