import React, { useState, Suspense } from "react";
import { useNavigate } from "react-router-dom";
import ServerDown from "../Components/ServerDown";

const TechnicalDetails = ({ portal }) => {
  const nav = useNavigate();
  const [message, setMessage] = useState("");

  const copyToClipboard = () => {
    setMessage("Copied");
  };

  return (
    <div>
      {message ? (
        <div
          className="ui alert alert-warning alert-dismissible fade show"
          role="alert"
        >
          {message}

          <button
            type="button"
            className="btn-close"
            aria-label="Close"
          ></button>
        </div>
      ) : null}

      <Suspense fallback={<h1>df</h1>}>
        {portal ? (
          <>
            <div>
              <img src="../../database.svg" alt="" width="30" />
              <span>
                <strong> Database Name</strong>
              </span>{" "}
              : {portal.databaseName}
            </div>
            <div className="copy-board">
              <img src="../../../database2.svg" alt="" width="25" />
              <span>
                <strong>Database IP</strong> : {portal.databaseIP}
              </span>
              <span onClick={copyToClipboard}>
                <i className="bi bi-clipboard"></i>
              </span>
            </div>
            <div className="copy-board">
              <img src="../../../server.svg" alt="server" width="25" />
              <span>
                <strong>SERVER IP</strong> : {portal.databaseIP}
              </span>
              <i className="bi bi-clipboard"></i>
            </div>
            <div className="copy-board">
              <img src="../../../link.svg" alt="server" width="20" />
              {portal.ApiEndPoint}
              <span>
                <div
                  onClick={() => nav(`${portal.ApiEndPoint}`)}
                  target="_blank"
                >
                  <i className="bi bi-box-arrow-up-right"></i>
                </div>
              </span>
            </div>
            <div className="copy-board">
              <img src="../../../port.svg" alt="server" width="25" />
              <strong>PORT</strong> <strong>{portal.Port}</strong>
            </div>
          </>
        ) : (
          <ServerDown />
        )}
      </Suspense>
    </div>
  );
};

export default TechnicalDetails;
