import React, { useState, Suspense } from "react";
import ServerDown from "../Components/ServerDown";

const BasicInformation = ({ portal }) => {
  const [message, setMessage] = useState("");
  // const [fileToDownload, setFileToDownload] = useState("");
  // const [message, setMessage] = useState("");

  const copyToClipboard = () => {
    var copyText = document.getElementById("copy-board").value;
    navigator.clipboard.writeText(copyText).then(() => {
      setMessage("Link Copied");
      alert("Copied to clipboard");
    });

    setTimeout(() => setMessage(""), 2000);
  };

  return (
    <Suspense fallback={<h2>dfs</h2>}>
      {portal ? (
        <div>
          <div className="navbar-brand">
            <strong>Application Name: {portal.appName}</strong>
          </div>

          <div className="navbar-brand">
            <strong>Brief Description: {portal.description}</strong>
          </div>

          <div>
            <strong>Developer(s): {portal.Developer}</strong>
          </div>

          <div className="copy-board">
            {message ? (
              <div className="copy-board copy center">Copied</div>
            ) : (
              <>
                <strong> Live URL:</strong>{" "}
                <span id="copy-board">{portal.liveURL}</span>
                <span>
                  <i
                    className="bi bi-box-arrow-up-right"
                    onClick={copyToClipboard}
                  ></i>
                </span>
              </>
            )}
          </div>

          <div className="copy-board">
            <span>
              <img src="../../../cyberark.png" alt="CyberArk" width={35} />
            </span>
            <span className="link center">
              https://secureark.keystonebankng.com/PasswordVault/v10/Accounts
            </span>
            <span>
              <i
                className="bi bi-box-arrow-up-right"
                onClick={copyToClipboard}
              ></i>
            </span>
          </div>
        </div>
      ) : (
        <ServerDown />
      )}
    </Suspense>
  );
};

export default BasicInformation;
